.circleDesignStyle {
  .out {
    border-radius: 28px;
    border: 6px solid var(--Success-50, #ecfdf3);
    background: var(--Success-100, #dcfae6);
    padding: 6px;
  }
}

.taskTitleInput {
  color: var(--Gray-900, #101828);

  /* Title subsection */
  font-family: Inter;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: -0.33px;
  padding: 10px 14px;
  border: 1px solid #fff;
  position: relative;
  left: -12px;

  &:focus {
    padding: 10px 14px;
    border-radius: 4px;
    border: 1px solid var(--Gray-300, #d0d5dd);
  }
}

.formLabel {
  color: var(--Gray-600, #475467);

  /* body default medium */
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 22px; /* 157.143% */
  letter-spacing: 0.14px;
}

.taskDescriptionInput {
  color: var(--Gray-900, #101828);
  overflow: hidden;
  /* Text md/Regular */
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
  padding: 10px 14px;
  border: 1px solid #fff;
  outline: none !important;
  position: relative;
  left: -10px;
  min-height: 200px;
  &::placeholder {
    color: var(--Gray-400, #98a2b3);

    /* Text md/Regular */
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 150% */
  }

  &:focus {
    padding: 10px 14px;
    border-radius: 4px;
    border: 1px solid var(--Gray-300, #d0d5dd) !important;
    box-shadow: none !important;
  }
}

.projectDropdown {
  left: -10px;
  position: relative;
  border: none;
  &:focus {
    border: 1px solid var(--Gray-300, #d0d5dd) !important;
  }
}

.messageInputIcons {
  gap: 8px;
  top: 65%;
  right: 8px;
  display: flex;
  position: absolute;
  align-items: flex-end;
  transform: translateY(-50%);
}

.attachButton,
.emojiButton {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 3px;
  border-radius: 6px;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: var(--gray-100, #f2f4f7);
  }

  &:active {
    background-color: var(--gray-200, #eaecf0);
  }
}

.commentButton {
  font-size: 10px !important;
  background-color: #158ecc !important;
  border-color: #158ecc !important;
  color: white !important;
  font-weight: 500 !important;
  padding: 4px 6px 4px 6px !important;
  border-radius: 40px !important;
  transition: all 0.2s ease !important;
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
  margin-bottom: 2px;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05) !important;

  &:hover {
    font-size: 10px !important;
    background-color: #158ecc !important;
    box-shadow: 0px 1px 3px rgba(16, 24, 40, 0.1) !important;
  }

  &:active {
    font-size: 10px !important;
    background-color: #158ecc !important;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05) !important;
  }

  &:disabled {
    font-size: 10px !important;
    background-color: #158ecc !important;
    border-color: #158ecc !important;
    box-shadow: none !important;
    opacity: 0.5 !important;
  }

  i {
    font-size: 16px !important;
    margin-left: 4px !important;
  }
}